<template>
    <div id="edit-patient">
        <h3>Edit Patient</h3>
        <div class="row">
          <form @submit.prevent="updatePatient" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input disabled type="text" v-model="patient_id" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="name" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="guardian_name" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="guardian_email" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="guardian_phone" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="position" required>
              </div>
            </div>
            <button type="submit" class="btn">Submit</button>
            <router-link to="/" class="btn grey">Cancel</router-link>
          </form>
        </div>
    </div>
    </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "edit-patient",
  data() {
    return {
      patient_id: null,
      name: null,
      guardian_name: null,
      guardian_email: null,
      guardian_phone: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db
      .collection("patients")
      .where("patient_id", "==", to.params.patient_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.patient_id = doc.data().patient_id;
            vm.name = doc.data().name;
            vm.guardian_name = doc.data().guardian_name;
            vm.guardian_email = doc.data().guardian_email;
            vm.guardian_phone = doc.data().guardian_phone;
            vm.position =
              doc
                .data()
                .position.charAt(0)
                .toUpperCase() + doc.data().position.slice(1);
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db
        .collection("patients")
        .where("patient_id", "==", this.$route.params.patient_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.patient_id = doc.data().patient_id;
            this.name = doc.data().name;
            this.guradian_name = doc.data().guradian_name;
            this.guradian_email = doc.data().guradian_email;
            this.guradian_phone = doc.data().guradian_phone;
            this.position = doc.data().position;
          });
        });
    },
    updatePatient() {
      db
        .collection("patients")
        .where("patient_id", "==", this.$route.params.patient_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                patient_id: this.patient_id,
                name: this.name,
                guardian_name: this.guardian_name,
                guardian_email: this.guardian_email,
                guardian_phone: this.guardian_phone,
                position: this.position
              })
              .then(() => {
                this.$router.push({
                  name: "view-patient",
                  params: { patient_id: this.patient_id }
                });
              });
          });
        });
    }
  }
};
</script>



