<template>
    <div id="new-patient">
        <h3>New Patient</h3>
        <div class="row">
          <form @submit.prevent="savePatient" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="patient_id" required>
                <label>Patient ID#</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="name" required>
                <label>Name</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="guardian_name" required>
                <label>Guardian Name</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="guardian_email" required>
                <label>Guardian Email</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="guardian_phone" required>
                <label>Guardian Phone</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="position" required>
                <label>Position</label>
              </div>
            </div>
            <button type="submit" class="btn">Submit</button>
            <router-link to="/" class="btn grey">Cancel</router-link>
          </form>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "new-patient",
  data() {
    return {
      patient_id: null,
      name: null,
      guardian_name: null,
      guardian_email: null,
      guardian_phone: null,
      position: null
    };
  },
  methods: {
    savePatient() {
      db
        .collection("patients")
        .add({
          patient_id: this.patient_id,
          name: this.name,
          guardian_name: this.guardian_name,
          guardian_email: this.guardian_email,
          guardian_phone: this.guardian_phone,
          position: this.position
        })
        .then(docRef => this.$router.push("/"))
        .catch(error => console.log(error));
    }
  }
};
</script>



