<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Patients</h4>
            </li>
            <li v-for="patient in patients" v-bind:key="patient.id" class="collection-item">
                <div class="chip">
                    {{patient.position.charAt(0).toUpperCase() + patient.position.slice(1)}}    
                </div>
                    {{patient.patient_id}} : {{patient.name}}

                    <router-link class="secondary-content" v-bind:to="{name: 'view-patient', params: {patient_id: patient.patient_id}}">
                        <i class="fa fa-eye"></i>
                    </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashbaord",
  data() {
    return {
      patients: []
    };
  },
  created() {
    db
      .collection("patients")
      .orderBy("patient_id")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            patient_id: doc.data().patient_id,
            name: doc.data().name,
            guardian_name: doc.data().guardian_name,
            guardian_email: doc.data().guardian_email,
            guardian_phone: doc.data().guardian_phone,
            position: doc.data().position
          };
          this.patients.push(data);
        });
      });
  }
};
</script>



