<template>
    <div id="view-patient">
        <ul class="collection with-header">
          <li class="collection-header">
            <h4>{{name}}</h4>
          </li>
          <li class="collection-item">Patient ID#: {{patient_id}}</li>
          <li class="collection-item">Guardian Name: {{guardian_name}}</li>
          <li class="collection-item">Guardian Email: {{guardian_email}}</li>
          <li class="collection-item">Guardian Phone: {{guardian_phone}}</li>
          <li class="collection-item">Position: {{position}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deletePatient" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-patient', params: {patient_id: patient_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "view-patient",
  data() {
    return {
      patient_id: null,
      name: null,
      guardian_name: null,
      guardian_email: null,
      guardian_phone: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db
      .collection("patients")
      .where("patient_id", "==", to.params.patient_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.patient_id = doc.data().patient_id;
            vm.name = doc.data().name;
            vm.guardian_name = doc.data().guardian_name;
            vm.guardian_email = doc.data().guardian_email;
            vm.guardian_phone = doc.data().guardian_phone;
            vm.position =
              doc
                .data()
                .position.charAt(0)
                .toUpperCase() + doc.data().position.slice(1);
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db
        .collection("patients")
        .where("patient_id", "==", this.$route.params.patient_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.patient_id = doc.data().patient_id;
            this.name = doc.data().name;
            this.guradian_name = doc.data().guradian_name;
            this.guradian_email = doc.data().guradian_email;
            this.guradian_phone = doc.data().guradian_phone;
            this.position = doc.data().position;
          });
        });
    },
    deletePatient() {
      if (confirm("確定嗎?")) {
        db
          .collection("patients")
          .where("patient_id", "==", this.$route.params.patient_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>



